			dotStyle: false
		};
	},
	mixins: [mixin],
	onLoad() {
		this.getLocation();
		this.getInfo();
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		getInfo(){
			http.get('/data/getTimelineService').then(res => {
				console.log(res)
			})
		},
		getLocation() {
			getLocation().then(res => {
				const { longitude, latitude } = res;
				console.log(res)
				this.getLocationInfo({ longitude, latitude });
			});
		},
		// 获取位置信息
		getLocationInfo (location) {
			let that = this;
		  reverseGeocoder(location)
		    .then(res => {
		      // console.log('当前位置信息：', res)
		      const address = res.result.pois[0].title
						// console.log(address) 
						// console.log(result)
						that.address = address;
						console.log(that.address)
		    })
		    .catch(err => {
		    })
		},
		selectAddress() {
			let that = this;

			uni.chooseLocation({
				success: function(res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
					that.address = res.name;
				}
			});
		},
		goCart() {
			uni.navigateTo({
				url: '../cart/index'
			});
		},
		goWash() {
			uni.navigateTo({
				url: './detail/wash'
			});
		},
		goToUrl(url, title) {

			// goToUrl(url){
			// location.href(url);
			uni.navigateTo({
				url: 'webpage/index?url=' + url + '&title=' + title
			});








		},
		goDetail() {
			uni.navigateTo({
				url: './detail/index'
			});
		}
	}
};
